@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  /* background-color: #1a1a1c; */
  /* color: #fff */
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 0;
  /* color: #fff; */
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text {
  margin-top: 32px;
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(135deg, orange 60%, cyan);
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: white;
}